
@font-face {
  font-family: "NetlifeSans";
  src: url("/assets/fonts/Netlife-Regular.eot?#iefix") format("embedded-opentype"), url("/assets/fonts/Netlife-Regular.otf") format("opentype"), url("/assets/fonts/Netlife-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
html {
  font-size: 20px;
}
body {
  font-family: "Netlife-Regular", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #2c3e50;
  padding: 2%;
}
img {
  max-width: 100%;
  height: auto;
}
nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 80px;
}
nav :nth-of-type(1) {
    -webkit-box-flex: 3;
        -ms-flex-positive: 3;
            flex-grow: 3;
}
nav a {
    font-size: 1rem;
    margin-right: 20px;
}
